<template>
  <div class="layout">
    <SidebarMenu class="sidebar" :collapsed="collapsed" @toggle-collapse="toggleCollapse" /> <!-- 左侧导航栏 -->
    <div class="main-content">
      <LayoutNavbar class="navbar" /> <!-- 顶部导航栏 -->
      <div class="content">
        <router-view /> <!-- 路由页面内容 -->
      </div>
    </div>
  </div>
</template>

<script>
import SidebarMenu from '@/components/SidebarMenu.vue';
import LayoutNavbar from '@/components/LayoutNavbar.vue';

export default {
  name: 'AppLayout',
  components: {
    SidebarMenu,
    LayoutNavbar,
  },
  data() {
    return {
      collapsed: false, // 控制侧边栏折叠状态
    };
  },
  methods: {
    toggleCollapse() {
      this.collapsed = !this.collapsed;
    },
  },
};
</script>

