}

.sidebar {
  transition: width 0.3s;
}

.main-content {
  flex: 1; /* 主内容区域填充剩余空间 */
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.navbar {
  height: 60px;
  line-height: 60px;
  background-color: #ffffff;
  padding: 0 20px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ddd; /* 添加下边框让导航栏和侧边栏视觉连接 */
}

.content {
