<template>
  <el-aside :width="collapsed ? '66px' : '200px'" class="sidebar">
    <!-- 收起时，按钮单独在最上方 -->
    <div v-if="collapsed" class="collapse-btn-wrapper">
      <el-button
       class="collapse-btn1"
        :icon="collapsed ? 'menu' : 'ArrowLeft'"
        @click="toggleCollapse"
        size="small"
        circle
      />
    </div>

    <div v-else class="sidebar-header">
      <!-- 展开时显示 Logo 和按钮 -->
      <div class="sidebar-logo">
        <a href="/">
          <img :src="logo" alt="Logo" class="logo-img" />
        </a>
      </div>
      <el-button
        class="collapse-btn"
        :icon="collapsed ? 'menu' : 'ArrowLeft'"
        @click="toggleCollapse"
        size="small"
        circle
      />
    </div>

    <el-menu
      :default-active="activeMenu"
      class="el-menu-vertical"
      router
      :collapse-transition="false"
      :collapse="collapsed"
    >
      <el-menu-item index="/home">
        <House class="menu-icon" />
        <span v-if="!collapsed" class="menu-text">首页</span>
      </el-menu-item>
      <el-menu-item index="/security">
        <Lock class="menu-icon" />
        <span v-if="!collapsed" class="menu-text">代码智能加固</span>
      </el-menu-item>
      <el-menu-item index="/chat">
        <ChatDotSquare class="menu-icon" />
        <span v-if="!collapsed" class="menu-text">AI对话</span>
      </el-menu-item>
      <el-menu-item index="/data-management">
         <DataAnalysis class="menu-icon" />
         <span v-if="!collapsed" class="menu-text">数据管理</span>
      </el-menu-item>
    </el-menu>
  </el-aside>
</template>

<script>
export default {
  name: 'SidebarMenu',
  data() {
    return {
      logo: require('@/assets/logo2.jpg'), // 引用 assets 文件夹下的 logo
      collapsed: false, // 控制折叠状态
    };
  },
  computed: {
    // 获取当前路由路径，绑定到菜单的 default-active
    activeMenu() {
      return this.$route.path;
    },
  },
  methods: {
    toggleCollapse() {
      this.collapsed = !this.collapsed;
      this.$emit('toggle-collapse');
    },
  },
};
</script>

<style scoped>
.sidebar {
  height: 100vh;
  overflow-y: auto;
  background-color: #f5f5f5;
  border-right: 1px solid #ddd;
  display: flex;
  flex-direction: column; /* 垂直布局 */
  transition: width 0.3s ease; /* 添加过渡效果 */
}

/* 侧边栏头部样式 */
.sidebar-header {
  display: flex;
  align-items: center;
  justify-content: center; /* 内容居中 */
  padding: 10px;
  position: relative; /* 为按钮定位提供上下文 */
}

/* 收起时，按钮单独在最上方 */
.sidebar.el-aside--collapse .sidebar-header {
  justify-content: center;
}

.collapse-btn-wrapper {
  background-color: #ffffff; /* 背景颜色 */
  display: flex;
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
  padding: 0px;
  width: 63.47px;
  height: 60px;
  border-right: 1px solid #ddd; /* 右侧灰色边框 */
  
}


/* Logo 容器 */
.sidebar-logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-img {
  max-height: 60px;
  max-width: 100%;
  object-fit: contain;
}

/* 折叠按钮 */
.collapse-btn {
  background-color: #ffffff;
  border: none;
  font-size: 18px;
  position: absolute; /* 绝对定位，使按钮位于右侧 */
  right: 10px; /* 右侧距离 */
}
.collapse-btn1 {
  font-size: 16px;
  width: 16px;
  height: 16px;
  margin-right: 6px;
 
}

.sidebar.el-aside--collapse .collapse-btn {
  position: static; /* 收起时按钮不需要绝对定位 */
}

.collapse-btn .el-icon {
  font-size: 20px; /* 调整图标大小 */
  color: #0d49e3; /* 设置图标颜色 */
}

/* 图标的大小和颜色 */
.menu-icon {
  font-size: 16px;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  color: #333;
}

.menu-text {
  color: #333;
  transition: color 0.3s;
}

.el-menu-item.is-active {
  color: #409eff;
}

.el-menu-item.is-active .menu-icon,
.el-menu-item.is-active .menu-text {
  color: #409eff;
}

.el-menu-item:hover {
  color: #66b1ff;
}

.el-menu-item:hover .menu-icon,
.el-menu-item:hover .menu-text {
  color: #66b1ff;
}
</style>
